import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import React from "react";
import { IconLink, IconLinkProps } from "../IconLink";

type ProfileProps = {
    name: string
    job_title: string
    birthday: string
    email: string
    education: string
    about: string
    links: IconLinkProps[]
}

const ResumeProfile: React.FunctionComponent<ProfileProps> = (props: ProfileProps) => {
    const {
        name,
        links,
        job_title,
        birthday,
        email,
        education,
        about
    } = props;

    return <>
        <div className={ classNames("d-flex", "align-items-center", "justify-content-between", "g-mb-5") }>
            <h2 className={ classNames("g-font-weight-300", "g-mr-10") }>{ name }</h2>
            <ul className={ classNames("list-inline", "mb-0") }>
                {
                    links.map((item: any) => {
                        return <IconLink key={ item.title } { ...item }/>
                    })
                }
            </ul>
        </div>
        <h6 className={ classNames("h6", "g-font-weight-300", "g-mb-10") }>
            <i className={ classNames("g-pos-rel", "g-top-1", "g-mr-5", "g-color-gray-dark-v5") }>
                <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                 icon={ {
                                     prefix: "fas",
                                     iconName: 'award'
                                 } }/>
            </i>
            { job_title }
        </h6>
        <ul className={ classNames("list-inline", "g-font-weight-300") }>
            {
                birthday && <li className={ classNames("list-inline-item", "g-mr-20") }>
                    <i className={ classNames( "g-pos-rel", "g-top-1", "g-mr-8", "g-color-gray-dark-v5") }>
                        <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                         icon={ {
                                             prefix: "fas",
                                             iconName: 'birthday-cake'
                                         } }/>
                    </i>
                    { birthday }
                </li>
            }
            {
                email && <li className={ classNames("list-inline-item", "g-mr-20") }>
                    <i className={ classNames("g-pos-rel", "g-top-1", "g-mr-8", "g-color-gray-dark-v5") }>
                        <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                         icon={ {
                                             prefix: "fas",
                                             iconName: 'at'
                                         } }/>
                    </i>
                    { email }
                </li>
            }
            { education && <li className={ classNames("list-inline-item", "g-mr-20") }>
                <i className={ classNames("g-pos-rel", "g-top-1", "g-mr-8", "g-color-gray-dark-v5") }>
                    <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                     icon={ {
                                         prefix: "fas",
                                         iconName: 'graduation-cap'
                                     } }/>
                </i>
                { education }
            </li> }
        </ul>
        <hr className={ classNames("g-brd-gray-light-v4", "g-my-20") }/>
        <p className={ classNames("lead", "g-line-height-18") }>
            { about }
        </p>
    </>
}

export default ResumeProfile;
