import classNames from "classnames";
import { graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import React from "react"
import Layout from "../component/layout";
import ResumeProfile from "../component/resume/profile";

const About = (props: any) => {
    const {
        configYaml: profile,
        my: image
    }: any = props.data

    return <Layout title={ "關於我" }>
        <div className={ classNames('g-brd-around', 'g-brd-gray-light-v4', 'g-pa-20', 'g-mb-40') }>
            <div className={ 'row' }>
                <div className={ classNames('col-lg-4', 'g-mb-40', 'g-mb-0--lg') }>
                    <div className={ 'g-py-30--lg' }>
                        <GatsbyImage
                            className={ classNames('img-fluid', 'w-100') }
                            image={ getImage(image) }
                            alt="Bruno Jan"/>
                    </div>
                </div>
                <div className={ 'col-lg-8' }>
                    <ResumeProfile { ...profile } />
                </div>
            </div>
        </div>
    </Layout>
}

export const pageQuery = graphql`
    query {
        configYaml {
            name
            about
            id
            job_title
            links {
                url
                title
                icon {
                    iconName
                    prefix
                }
            }
        }
        my: imageSharp(fluid: {originalName: {eq: "myimage.jpg"}}) {
            gatsbyImageData(width: 300)
        }
    }
`

export default About;
