import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import React, { FunctionComponent } from "react";

export type IconLinkProps = {
    icon: IconProp
    url: string
}

export const IconLink: FunctionComponent<IconLinkProps> = (props: IconLinkProps): JSX.Element => {
    const {
        icon,
        url
    }: any = props
    return <li className={ classNames("list-inline-item", "g-mx-2") }
    >
        <a className={ classNames("u-icon-v1",
                                  "u-icon-size--sm",
                                  'u-icon-slide-up--hover',
                                  'g-color-gray-light-v1',
                                  "g-bg-gray-light-v5",
                                  "g-color-gray-light-v1--hover",
                                  "rounded-circle") }
           href={ url }
           target={ "_blank" }>
            <i className={ classNames("g-font-size-default", "g-line-height-1", "u-icon__elem-regular") }>
                <FontAwesomeIcon width={ 8 } height={ 14 }
                                 icon={ icon }/>
            </i>
            <i className={ classNames("g-font-size-default", "g-line-height-1", "u-icon__elem-hover") }>
                <FontAwesomeIcon width={ 8 } height={ 14 }
                                 icon={ icon }/>
            </i>
        </a>
    </li>;
};
